import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class DimensionsSetupService {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }

  public bmlfonGetDimensionSetupDetail(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetDimensionSetupDetail`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonSaveDimensionSetupDetail(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveDimensionSetupDetail`;
    return this.httpExt.post(url, reqdata, null);
  }
  
  public bmlfonDeleteDimensionSetupDetail(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteDimensionSetupDetail`;
    return this.httpExt.post(url, reqdata, null);
  }

}
