import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class TDSSetupService {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }

  public bmlfonGetTDSSetupDetail(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetTDSSetupDetail`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonSaveTDSSetupDetail(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveTDSSetupDetail`;
    return this.httpExt.post(url, reqdata, null);
  }
  
  public bmlfonDeleteTDSSetupLine(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteTDSSetupLine`;
    return this.httpExt.post(url, reqdata, null);
  }

}
