import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from '../../core/guards/auth.guard';

import { FinanceConfigurationComponent } from './finance-configuration.component';
import { MultiDimensionalSetupComponent } from  './components/multi-dimensional-setup/multi-dimensional-setup.component';
import { DimensionsSetupComponent } from './components/dimensions-setup/dimensions-setup.component';
import { UserSetupComponent } from './components/user-setup/user-setup.component';
import { AccountSegmentSetupComponent } from './components/account-segment-setup/account-segment-setup.component';
import { RetainEarningAccountSetupComponent } from './components/retain-earning-account-setup/retain-earning-account-setup.component';
import { AccountGroupSequenceSetupComponent } from './components/account-group-sequence-setup/account-group-sequence-setup.component';
import { TypeMasterNinetyNineComponent } from './components/1099-type-setup/type-master-ninety-nine/type-master-ninety-nine.component';
import { TDSSetupComponent } from './components/tds-setup/tds-setup.component';


const routes: Routes = [
  {
    path: '',
    component: FinanceConfigurationComponent,
    children:
      [
       
        {
          path: 'multi-dimensional-setup', component: MultiDimensionalSetupComponent, canActivate: [AuthGuard]
        },
        {
          path: 'dimensions-setup', component: DimensionsSetupComponent, canActivate: [AuthGuard]
        },
        {
          path: 'user-setup', component: UserSetupComponent, canActivate: [AuthGuard]
        },
        {
          path: 'account-segment-setup', component: AccountSegmentSetupComponent, canActivate: [AuthGuard]
        },
        {
          path: 'retain-earning-account-setup', component: RetainEarningAccountSetupComponent, canActivate: [AuthGuard]
        },
        {
          path: 'account-group-sequence-setup', component: AccountGroupSequenceSetupComponent, canActivate:[AuthGuard]
        },
        {
          path: '1099-type-setup', component: TypeMasterNinetyNineComponent, canActivate:[AuthGuard]
        },
        {
          path: 'tds-setup', component: TDSSetupComponent, canActivate:[AuthGuard]
        }
      ]
  }
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FinanceConfigurationRoutingModule { }
