import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class AccountSegmentSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
   
    public bmlfSaveSegmentData(headerData: any, formMode) {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveSegmentData`;
            return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetAccountSegmentSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetAccountSegmentSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
   
    public bmlfUpdateFormatData(headerData: any, formMode) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateMasking`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    
}