import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class RetainEarningAccountSetupService {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }

  public bmlfonGetRetainEarningSetup(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetRetainEarningSetup`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonSaveRetainEarningSetup(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveRetainEarningSetup`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGenerateRetainEarningAccount(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GenerateRetainEarningAccount`;
    return this.httpExt.post(url, reqdata, null);
  }
}
