import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class MultiDimensionalSetupService {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }

  public bmlfonGetMultiDimensionSetup(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetMultiDimensionSetup`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonSaveMultiDimensionSetup(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveMultiDimensionSetup`;
    return this.httpExt.post(url, reqdata, null);
  }
  
}
