import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class AccountGroupSequenceSetupService {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }

  public bmlfonGetAcGroupSequenceData(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetAcGroupSequenceData`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonUpdateAcGroupSequenceData(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateAcGroupSequenceData`;
    return this.httpExt.post(url, reqdata, null);
  }
    
}
