import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class UserSetupService {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }

  public bmlfonGetUserSetupDetail(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUserSetupDetail`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonSaveUserSetupDetail(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveUserSetupDetail`;
    return this.httpExt.post(url, reqdata, null);
  }

}
