import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class TypeSetupNinetyNineService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDelete1099HeaderAndDetail(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/Delete1099HeaderAndDetail`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfDelete1099SetupDetail(headerData: any) {
      const url = `${this.appSettings.ServerAPIEndpoint}/bme/Delete1099SetupDetail`;
      return this.httpExt.post<any>(url, headerData, null);
   }

    public bmlfSave1099Setup(headerData: any) {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateUpdate1099Detail`;
            return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGet1099SetupHeader(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/Get1099SetupHeaderDetail`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGet1099SetupDetail(headerData: any) {
      const url = `${this.appSettings.ServerAPIEndpoint}/bme/Get1099SetupDetail`;
      return this.httpExt.post<any>(url, headerData, null);
  }
}